import { Injectable } from '@angular/core';
import { BsModalService } from 'ngx-bootstrap/modal';
import { IconSelectorModalComponent } from './icon-selector-modal/icon-selector-modal.component';

@Injectable({ providedIn: 'root' })
export class IconSelectorService {
  constructor(protected modal: BsModalService) {}

  selectIcon(
    initialState: Partial<
      Pick<
        IconSelectorModalComponent,
        'currentSelection'
        | 'title'
        | 'saveButtonLabel'
        | 'iconCategoriesToExclude'
        | 'showIconClass'
      >
    > = {}
  ): Promise<string> {
    const modal = this.modal.show(IconSelectorModalComponent, {
      ignoreBackdropClick: true,
      keyboard: false,
      initialState,
      class: 'modal-lg'
    });
    const content = modal.content as IconSelectorModalComponent;
    return content.result;
  }
}
