import { Component } from '@angular/core';
import { gettext } from '@c8y/ngx-components';
import { BsModalRef } from 'ngx-bootstrap/modal';

@Component({
  selector: 'c8y-icon-selector-modal',
  templateUrl: './icon-selector-modal.component.html'
})
export class IconSelectorModalComponent {
  title: string = gettext('Select icon');
  currentSelection: string;
  saveButtonLabel: string = gettext('Select');
  iconCategoriesToExclude: string[] = [];
  showIconClass = true;

  readonly result: Promise<string> = new Promise((resolve, reject) => {
    this.save = resolve;
    this.cancel = reject;
  });

  private save: (value: string) => void;
  private cancel: (reason?: any) => void;

  constructor(private bsModal: BsModalRef) {}

  close() {
    this.bsModal.hide();
    this.cancel();
  }

  saveChanges(): void {
    this.bsModal.hide();
    this.save(this.currentSelection);
  }

  selectionChange(newSelection: string) {
    this.currentSelection = newSelection;
  }
}
